.main {
    height: 100vh;
    align-items: center;
    background: url(../img/main-bg.png) center right no-repeat;
    background-color: #eee;

    h1 {
        @include mainHeader;
    }

    // .main__content

    &__content {
        height: 100%;
        display: grid;
        align-items: center;
        align-content: center;
        justify-items: start;
        // width: 50%;
    }

    // .main__text

    &__text {
        @include text;
        margin-bottom: 17px;
    }

    // .main__review

    &__review {
        display: flex;
        align-items: center;
        gap: 20px;
        margin-bottom: 40px;
    }

    // .main__review-image

    &__review-image {
    }

    // .main__review-info

    &__review-info {
    }

    // .main__review-author

    &__review-author {
        @include cardTitle;
        margin-bottom: 10px;
    }

    // .main__review-rating

    &__review-rating {
    }

    // .main__btn

    &__btn {
        @include btn;
        background: #FFA332;
    }

