.nav {
    display: flex;
    gap: 40px;
}
.header {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    padding: 15px 0;
    background-color: $white;
    z-index: 11;
    // .header__inner
    &__inner {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    // .header__logo

    &__logo {
    }

    // .header__btn

    &__btn {
        @include btn;
    }
}
.menu {
    display: flex;
    gap: 20px;

    // .menu__item

    &__item {
    }

    // .menu__link

    &__link {
        @include navLink;
    }
}
